.ev-leaflet {
  width: 100%;
  height: 300px;
  margin-bottom: 20px;
  left: -10px;
}
.ev-leaflet img.leaflet-marker-icon {
  cursor: -webkit-grab;
  cursor: -moz-grab;
}
.ev-leaflet img.leaflet-marker-icon.leaflet-marker-draggable {
  cursor: pointer;
}
.modal-body .angular-leaflet-map {
  min-width: 540px;
}
