.notification-bar {
  height: 29px;
  border: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1039;
  line-height: 29px;
  text-align: center;
  color: #FFF;
  -webkit-transition: all ease-in-out 0.3s;
  transition: all ease-in-out 0.3s;
}
.notification-bar > .inner {
  position: absolute;
  height: 100%;
  width: 100%;
  background: #33cc99;
  padding-right: 40px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
