  background: #ef6e45;
}
.notification-bar > .inner.warning {
  background: #f79400;
}
.notification-bar > .inner.info {
  background: #6aabde;
}
.notification-bar > .inner.success {
  background: #72ab49;
}
.notification-bar > .close {
  outline: none;
  position: absolute;
  line-height: 29px;
  top: 0;
  color: #FFF;
  box-shadow: none;
  right: 10px;
  opacity: .8;
}
.notification-bar > .inner {
  opacity: 1;
  -webkit-transition: all ease-in-out 0.3s;
  transition: all ease-in-out 0.3s;
}
